.mobile-header {
    display: none;
    padding: 20px 20px;
    font-size: rem(24px);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: $backgroundClr;

    .mobile-search-container {
        background-color: $backgroundClr;
        border: 1px solid $borderClr;
        box-shadow: 0 1px 2px rgb(0 0 0 / 8%), 0 4px 12px rgb(0 0 0 / 5%);
        height: 58px;
        padding: 8px 8px 8px 14px;
        border-radius: 40px;

        .mobile-search-btn {
            height: 100%;
            background-color: unset;
            width: 32px;
            border-radius: 50%;
            padding: 8px;
            cursor: pointer;

            img {
                margin-top: 2px;

            }
        }

        .search-input {
            input {
                border: unset;
                background-color: unset;
                width: 100%;
                font-family: cereal-book;
                padding: 0;

                &:focus-visible {
                    outline: unset;
                }

            }
        }

        .mobile-filter-btn {
            border: 1px solid $borderClr;
            border-radius: 50%;
            height: 100%;
            aspect-ratio: 1;

            .mobile-filter-svg {
                height: 14px;
            }
        }
    }

    &.home {
        background-color: unset;

    }

    @include for-mobile-layout {
        display: block;

    }
}