.stay-filter {
    .modal-head-container {
        min-height: 64px;
        padding: rem(30px);
    }

    .close-modal-btn {
        left: 15px;
        position: absolute;
        width: 18px;
        text-align: left;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        margin-left: 5px;

        &:hover {
            background-color: $footer;
        }

        .close-modal-img {
            transform: translate(10%, 10%);
            width: 17px;
            height: 17px;
        }
    }

    .filter-modal {
        animation-name: filter-modal;
        animation-duration: 10s;
        z-index: 1000;
        position: fixed;
        width: 50%;
        left: 50%;
        transform: translate(-50%, -36%);
        background: #FEFEFE;
        border-radius: 10px;
        animation-duration: 400ms;
        animation-iteration-count: 1;
        animation-fill-mode: both;

        .modal-title {
            margin: 0 auto;
            text-align: center;
            font-size: rem(16px);
        }

        .filter-form {
            border-top: 1px solid rgb(235, 235, 235);
            padding: rem(30px);
            overflow-y: scroll;
            // max-height: 60vh;
            height: 60vh;
            position:relative;

            .form-grid {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-gap: 15px;
            }

            .span {
                margin-left: 21px;
                color: $subtext;
            }

            .filter-type {
                .price-filter-container {
                    align-items: center;
                    justify-content: center;
                    padding: 0 10px;

                    .min-max-price-filter {
                        min-height: 60px;
                        width: 100%;
                        border-radius: 8px;
                        box-shadow: $btnBorderClr 0px 0px 0px 1px inset;
                        padding: 10px;

                        .price-container {
                            color: $btnBorderClr;
                        }

                        .price {
                            font-family: cereal-book;
                            padding: 5px 0;
                            font-size: 17px;
                            color: $secondary;

                            span {
                                margin-inline-end: 5px;
                            }
                        }
                    }

                    .minus-price-filter {
                        margin: 8px;
                        font-size: 16px;
                    }

                    .css-1hjq20a {
                        width: 100%;
                    }

                }

                .css-1d1r5q-MuiFormHelperText-root {
                    font-family: cereal-book;
                }

                .css-j204z7-MuiFormControlLabel-root {
                    height: 32px;
                }

                .css-1vgzwi7-MuiFormControl-root {
                    margin: 0;
                }

                .css-ahj2mt-MuiTypography-root {
                    font-size: rem(16px);
                }

                .css-1d1r5q-MuiFormHelperText-root {
                    font-size: 15px;
                    line-height: 18px;
                    margin-left: 36px;
                    color: $btnBorderClr
                }

                .css-k008qs {
                    width: 100%;

                    .css-1vgzwi7-MuiFormControl-root {
                        width: 100%;

                        .css-dmmspl-MuiFormGroup-root {
                            display: grid;
                            grid-template-columns: 1fr 1fr;
                            width: 100%;

                            .css-ahj2mt-MuiTypography-root {
                                font-family: cereal-book;
                            }
                        }
                    }
                }
            }

            & .filter-type:not(:first-child) {
                border-top: 1px solid rgb(235, 235, 235);
                margin-bottom: 2em;
                margin-top: 2em;
            }

            h2 {
                margin-bottom: 1em;
                margin-top: 1em;
            }

            p {
                margin: 0
            }
        }

        .filter-footer {
            border-top: 1px solid rgb(235, 235, 235);
            padding: 5px;

            p {
                cursor: pointer;
                text-decoration: underline;
                font-weight: 500;
                margin-top: 26px;
            }

            button {
                background-color: $secondary;
                color: $backgroundClr;
                margin-inline-end: 10px;
                padding: 16px 28px;
                border-radius: 10px;
                margin-top: 14px;

                &:hover {
                    background-color: $secondary;
                }
            }

            @keyframes filter-modal {
                0% {
                    bottom: -400vh;
                }

                100% {
                    bottom: -10vh;
                }
            }
        }

        .filter-form-btn {
            border: 1px solid $border;
            margin-top: rem(5px);
            margin-inline-end: rem(10px);
            padding: rem(16px) rem(28px);
            border-radius: rem(25px);

            &:hover {
                border: 1px solid $secondary;
            }

            &.active {
                background-color: $secondary;
                color: $backgroundClr;
                border: unset;
            }
        }
    }

    @include for-mobile-layout {
        .filter-modal {
            width: 90%;
            height: 90%;
            transform: translate(-50%, 0);
            top: 20px;

            .modal-title {
                font-size: 1.5rem;
            }

            .filter-form {
                height: 90%;
            }

            .filter-footer {
                position: sticky;
                bottom: -10px;
                background-color: $backgroundClr;
                padding: 20px 0;
            }
        }
    }
}

.screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    overflow-y: hidden;
    z-index: 500;

    &.header-open {
        top: 165px;
    }

    &.guest-open {
        background-color: unset;
    }
}