.categories-container {
    z-index: 100;
    justify-items: center;
    height: fit-content;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

    .category-container {
        height: 78px;
        opacity: 0.6;
        cursor: pointer;
        width: fit-content;

        &:hover {
            opacity: 1;
            box-shadow: inset 0px -2.3px 0 #d3c9c9;
        }

        &.active {
            box-shadow: inset 0px -2.3px 0 rgba(0, 0, 0, 0.932);
            opacity: 1;
        }
    }

    .category-img {
        height: 24px;
        width: 24px;
        max-width: 100%;
        margin: 0 auto;
    }

    p {
        font-size: 0.69rem;
        margin: 0;
        font-weight: 700;
        white-space: nowrap;
    }
}