.amenities-container {
    .amenities {
        padding-bottom: em(22px);
    }

    .stay-amenities {
        font-size: 17px;
        display: grid;
        grid-template-columns: repeat(auto-fill, 42.9%);
        column-gap: 61px;

        .amenite-img-container {
            padding-bottom: em(6px);

            .amenite-img {
                height: 38px;
                margin-right: 5px;
                aspect-ratio: 0.7;

                &.Wifi::before {
                    content: url('../../../svg/Wifi.svg');
                }

                &.Kitchen::before {
                    content: url('../../../svg/Kitchen.svg');
                }

                &.Shampoo::before {
                    content: url('../../../svg/Shower gel.svg');
                }

                &.Essentials::before {
                    content: url('../../../svg/Essentials.svg');
                }

                &.Private::before {
                    content: url('../../../svg/Private entrance.svg');
                }

                &.Hair::before {
                    content: url('../../../svg/Hair dryer.svg');
                }

                &.Hot::before {
                    content: url('../../../svg/Hot water.svg');
                }

                &.Htub::before {
                    content: url('../../../svg/Htub.svg');
                }

                &.Air::before {
                    content: url('../../../svg/Air conditioning.svg');
                }

                &.Fire::before {
                    content: url('../../../svg/Fire and kit.svg');
                }

                &.Free::before {
                    content: url('../../../svg/Free parking on premises.svg');
                }

                &.Smoke::before {
                    content: url('../../../svg/Smoke alarm.svg');
                }

                &.Safe::before {
                    content: url('../../../svg/Safe.svg');
                }

                &.Dryer::before {
                    content: url('../../../svg/Dryer.svg');
                }

                &.Heating::before {
                    content: url('../../../svg/Heating.svg');
                }

                &.check-in::before {
                    content: url('../../../svg/check-in experience.svg');
                }

                &.Hangers::before {
                    content: url('../../../svg/Hangers.svg');
                }

                &.Shower::before {
                    content: url('../../../svg/Shower gel.svg');
                }

                &.TV::before {
                    content: url('../../../svg/TV.svg');
                }

                &.Pool::before {
                    content: url('../../../svg/Pool.svg');
                }

                &.Washer::before {
                    content: url('../../../svg/Washer.svg');
                }

                &.Bed::before {
                    content: url('../../../svg/Bed lines.svg');
                }

                &.Wine::before {
                    content: url('../../../svg/Wine glasses.svg');
                }

                &.Gym::before {
                    content: url('../../../svg/Gym.svg');
                }

                &.Clothing::before {
                    content: url('../../../svg/Clothing storage.svg');
                }

                &.Bathub::before {
                    content: url('../../../svg/Bathub.svg');
                }

                &.Bedroom::before {
                    content: url('../../../svg/Bedroom.svg');
                }

                &.Body::before {
                    content: url('../../../svg/Body soap.svg');
                }

                &.Cleaning::before {
                    content: url('../../../svg/Cleaning products.svg');
                }

                &.Conditioner::before {
                    content: url('../../../svg/Conditioner.svg');
                }

                &.Dishes::before {
                    content: url('../../../svg/Dishes and silverware.svg');
                }

                &.Sea::before {
                    content: url('../../../svg/Sea view.svg');
                }

                &.Security::before {
                    content: url('../../../svg/Security cameras on property.svg');
                }

                &.Sound::before {
                    content: url('../../../svg/Sound system.svg');
                }

                &.street::before {
                    content: url('../../../svg/street parking.svg');
                }

                &.P::before {
                    content: url('../../../svg/P pool.svg');
                }

                &.Washer::before {
                    content: url('../../../svg/Washer.svg');
                }

                &.Pets::before {
                    content: url('../../../svg/Pets allowed.svg');
                }

                &.Long::before {
                    content: url('../../../svg/Long term.svg');
                }
            }

            .amenite {
                padding-left: 7px;
            }

        }
    }

    @include for-mobile-layout {
        .stay-amenities {
            grid-template-columns: 1fr
        }

        .amenite-img-container {
            font-size: 14px;
        }

        .amenite-img {
            height: 32px;
        }
    }
}