.app-header {
    position: fixed;
    height: 193px;
    width: 100%;
    background-color: unset;
    z-index: 450;
    transition: 1s;

    .header-container {
        .header-content-container {
            display: relative;

            >:first-child {
                width: 300px;
            }

            height: 80px;

            .user-nav-container {
                width: 300px;
                justify-content: flex-end;

                .user-details-container {
                    width: 77px;
                    height: 42px;
                    border: 1px solid $borderClr;
                    background-color: $backgroundClr;
                    padding-inline-start: 10px;
                    border-radius: 32px;
                    cursor: pointer;
                    transition: box-shadow 0.2s;

                    &:hover {
                        box-shadow: 0 1px 2px rgb(0 0 0 / 8%), 0 4px 12px rgb(0 0 0 / 5%);
                    }

                    .user-avatar {
                        height: 30px;
                        flex-grow: 1;
                    }

                    .user-img {
                        height: 34px;
                        flex-grow: 1;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 10px;
                        margin-top: 14px;
                    }

                    .user-menu {
                        height: 1rem;
                    }

                    .user-options-container {
                        position: relative;
                        opacity: 0;
                        transform: translateX(500px);
                        z-index: 1000;

                        &.open {
                            opacity: 1;
                            height: calc(50vh - 180px);
                            transform: translateX(3px);
                        }
                    }
                }

                .main-nav {
                    height: 42px;
                    gap: 5px;
                    min-width: fit-content;

                    a {
                        color: $backgroundClr;
                        padding: 12px 11px 5px 12px;
                        height: 100%;

                        &:hover {
                            color: $secondary;
                            border-radius: 32px;
                            background-color: rgba(235, 235, 235, 0.357);
                        }
                    }
                }
            }
        }



        .logo-img {
            width: 135px;

            .app-name {
                font-size: rem(23px);
                color: $primary;
                font-weight: 700;
                font-family: cereal-bold;
                letter-spacing: -0.04em;
            }
        }
    }

    &.close {
        height: unset;
        background-color: $backgroundClr;

        .header-content-container {
            .user-nav-container {
                gap: 15px;
                justify-content: flex-end;

                .main-nav {
                    a {
                        color: inherit;

                    }
                }
            }
        }
    }

    &.pages {
        border-bottom: 1px solid $borderClr;
        background-color: $backgroundClr;

        .header-content-container {
            a {
                color: $secondary;
            }
        }

        &.close {
            height: unset;

            .header-content-container {
                a {
                    color: inherit;
                }
            }
        }
    }

    @include for-mobile-layout {
        display: none;
    }
}

.dashboard-host-options {
    position: relative;

    .new-info-indicator {
        position: absolute;
        z-index: 1;
        cursor: pointer;
        height: 10px;
        min-width: 10px;
        top: 7px;
        right: 7px;
        box-shadow: 0 0 0 1.5px $backgroundClr;
        background-color: $primary;
        border-radius: 50%;
    }
}