.date-picker-inputs {
    width: 100%;
    height: 100%;

    >* {
        flex-grow: 1;
        border-radius: 40px;
        background-color: $footer;
        display: grid;
        place-content: center;

        &.open {
            box-shadow: rgba(0, 0, 0, 0.2) 0px 6px 20px 0px;
            background-color: $backgroundClr;
        }
    }
}