.user-options {
    background-color: $backgroundClr;
    border-radius: 12px;
    color: $secondary;
    margin-top: rem(34px);
    padding: 8px 0px;
    position: absolute;
    top: 50%;
    right: 5%;
    min-width: 250px;
    box-shadow: rgb(0 0 0 / 12%) 0px 2px 16px;
    font-family: cereal-book;

    .login-options-container {
        margin-top: 8px 0px;
        border-bottom: 1px solid $borderClr;

        .sign-up {
            font-family: cereal-bold;
        }
    }

    .login-options-container:first-child {
        margin-bottom: 4px;

    }

    .login-options-container>* {
        font-size: 14px;
        font-weight: 600;
        line-height: 18px;
        padding: 16px 14px;

        &:hover {
            background-color: $footer;
        }
    }

    .host-options-container {
        margin-top: 8px 0px;
    }

    .host-options-container:last-child {
        margin-top: 4px;
    }

    .host-options-container>* {
        font-size: 14px;
        line-height: 18px;
        padding: 16px 14px;

        &:hover {
            background-color: $footer;
        }
    }
}

.user-options-screen {
    transform: translateX(-94%);
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
}